@font-face {
  font-family: "Montserrat";
  src: url(../../font/Montserrat-Regular.woff);
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url(../../font/Montserrat-Light.woff);
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url(../../font/Montserrat-Medium.woff);
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
body {
  font-family: "Montserrat";
  color: #40474e;
  background-color: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.link {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.title {
  position: relative;
  font-family: "Montserrat", sans serif;
  font-size: 20px;
  line-height: 1.21;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.header {
  color: greenyellow;
}